
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.card-body {
  background-color: hsl(30, 38%, 92%);
  height: 100vh;
  position: relative;
  overflow: auto;
  
}

.card-body1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
 
}

.card-info {
  background-color: hsl(0, 0%, 100%);
  height: 400px;
  width: 300px;
  padding: 20px 30px;
  border-radius: 0 10px 10px 0;
}

.card-info h3 {
  font-size: 16px;
  color: hsl(228, 12%, 48%);
  letter-spacing: 5px;
  padding-top: 10px;
  padding-bottom: 20px;
}

.card-info h1 {
  font-family: 'fraunces';
  font-weight: 700;
  color: hsl(212, 21%, 14%);
  padding-bottom: 20px;
  line-height: 1;
  
}

.card-info p {
  font-size: 14px;
  color: hsl(228, 12%, 48%);
  font-family: 'montserrat';
  font-weight: 500;
  padding-bottom: 20px;
}

.card-info h4 {
  color: hsl(158, 36%, 37%);
  font-family: 'fraunces';
  font-size: 25px;
  padding-bottom: 20px;
}

.card-info h4 span {
  margin-left: 30px;
  color: hsl(228, 12%, 48%);
  font-size: 12px;
  text-decoration: line-through;
  font-family: montserrat;
  
}

.card-info button {
  background-color: hsl(158, 36%, 37%);
  border: none;
  padding: 10px;
  color: hsl(0, 0%, 100%);
  border-radius: 8px;
  width: 100%;
  margin-bottom: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.card-info button:hover {
  background-color: hsl(158, 46%, 19%);
}

button svg {
  margin-right: 10px;
  align-items: center;
}

/* .card-img {
  height: 400px;
} */



.img {
  height: 400px;
  border-radius: 10px 0 0 10px;
}

#myImage {
  display: none;
}

/* MEDIA QUERRY */

@media  screen and (max-width: 700px) {


  .card-body1 {
    display: flex;
    flex-direction: column;
    max-height: 100%;
    padding-top: 30px;
    padding-bottom: 30px !important;
   

  }

  .img {
    display: none;
  }

  #myImage {
    display: block;
    max-width: 100%;
    border-radius: 10px 10px 0 0;
    
  }

  .card-info {
    width: 400px;
    border-radius: 0 0 10px 10px;
  }

  .card-info h3 {
    padding-bottom: 10px;
    padding-top: 5px;
  }

  .card-info h1 {
    padding-bottom: 10px;
  }

  .card-info button {
    margin-bottom: 10px;
  }

}








